import React from "react";
import { TableCell, TableRow, Button } from "@material-ui/core";
import accounting from "accounting-js";
import { acceptRole } from "./../../../transactions";

const TeamMember = ({
  worker,
  payrate,
  shares,
  key,
  roleId,
  roleaccepted
}: any) => (
  <TableRow key={key}>
    <TableCell component="th" scope="row">
      {worker}
    </TableCell>
    <TableCell>{roleId}</TableCell>
    <TableCell align="right">{payrate}</TableCell>
    <TableCell align="right">
      {accounting.formatNumber(shares, { precision: 0 })}
    </TableCell>
    <TableCell align="right">
      {roleaccepted ? (
        <span>Accepted</span>
      ) : (
        <Button
          variant="contained"
          color="primary"
          onClick={() => acceptRole(roleId)}
        >
          Accept
        </Button>
      )}
    </TableCell>
  </TableRow>
);

export default TeamMember;
