import React, { Component } from "react";
import { Button, TextField, Typography } from "@material-ui/core";

export default class Form extends Component<any, any> {
  state = {
    decHours: "",
    notes: ""
  };

  onChange = (e: any) => {
    console.log(e.target.name);
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  submit = () => {
    this.props.onSubmit(this.state);
    this.setState({ decHours: "", notes: "" });
  };

  render() {
    return (
      <React.Fragment>
        <Typography variant="h5" gutterBottom>
          Enter Time
        </Typography>

        <TextField
          id="standard-name"
          label="Decimal Hours"
          name="decHours"
          value={this.state.decHours}
          required={true}
          type="number"
          onChange={this.onChange}
          margin="normal"
        />
        <TextField
          id="standard-name"
          label="Work notes"
          name="notes"
          value={this.state.notes}
          required={true}
          onChange={this.onChange}
          margin="normal"
          multiline={true}
        />

        <Button variant="contained" color="primary" onClick={this.submit}>
          Submit
        </Button>
      </React.Fragment>
    );
  }
}
