import React, { Component } from "react";
import SubPage from "./../../SubPage";
import Info from "./Info";
import Form from "./Form";
import Content from "./Content";
import { createWorkEntry } from "../../../transactions";
import { eos } from "../../../index";
import moment from "moment";
import { getWorkEntries } from "../../../api";

const getRole = async (
  accountName: string,
  organisation: string
): Promise<string> => {
  const result = await eos.getTable("roles").then(res => res.rows);
  return result.find(
    (role: any) => role.worker === accountName && role.org === organisation
  ).key;
};

interface WorkEntry {
  role: string;
  worker: string;
  dechours: string;
  rewardAmount: number;
  rewardSymbol: number;
  notes: string;
}

interface IState {
  workEntries: any[];
}

class WorkContainer extends Component<{ orgname: string }, IState> {
  state = {
    workEntries: []
  };

  componentDidMount() {
    this.fetchTimeEntries();
  }

  fetchTimeEntries = async () => {
    const timeEntries: any[] = await getWorkEntries(this.props.orgname);
    console.log(timeEntries);
    this.setState({
      workEntries: timeEntries.map(
        ({ dechours, notes, worker, reward }: any) => ({
          worker,
          decHours: moment.duration(dechours, "hours").humanize(),
          rewardAmount: reward.amount,
          rewardSymbol: reward.symbol,
          notes
        })
      )
    });
  };

  onSubmit = async (obj: any) => {
    try {
      const accountName = eos.account.name;
      const org = this.props.orgname;
      const role = await getRole(accountName, org);
      const result = await createWorkEntry(role, obj);
      console.log(result.transaction_id);
      setTimeout(() => this.fetchTimeEntries(), 1000);
    } catch (e) {
      console.log("eeee", e);
    }
  };

  render() {
    console.log(this.props.orgname, "was received at work prop");
    return (
      <SubPage
        content={<Content workEntries={this.state.workEntries} />}
        info={<Info />}
        form={<Form onSubmit={this.onSubmit} />}
      />
    );
  }
}

export default WorkContainer;
