import React, { Component } from "react";
import { TextField, FormControl } from "@material-ui/core";

const CreateTokenForm = ({
  issuer,
  symbol,
  maxSupply,
  precision,
  handleChange
}: any) => (
  <FormControl>
    <TextField
      id="standard-name"
      label="Issuer"
      value={issuer}
      name="issuer"
      onChange={handleChange}
      margin="normal"
    />
    <TextField
      id="standard-name"
      label="Symbol"
      placeholder="CONT"
      value={symbol}
      name="symbol"
      onChange={handleChange}
      margin="normal"
    />
    <TextField
      id="standard-name"
      label="Precision"
      value={precision}
      name="precision"
      onChange={handleChange}
      margin="normal"
    />
    <TextField
      id="standard-name"
      label="Max supply"
      value={maxSupply}
      name="maxSupply"
      placeholder="1000000"
      onChange={handleChange}
      margin="normal"
    />
  </FormControl>
);

export default CreateTokenForm;
