import React, { Component } from "react";
import { testReset } from "./../../transactions";
import { List } from "@material-ui/core";
import Org from "./Org";

class Content extends Component<any> {
  triggerReset = async () => {
    await testReset();
  };

  render() {
    return (
      <React.Fragment>
        <List component="nav">
          {this.props.organisations.map((org: any) => (
            <Org {...org} />
          ))}
        </List>
      </React.Fragment>
    );
  }
}

export default Content;
