import axios from "axios";
import { JsonRpc } from "eosjs";

const baseURL = process.env.REACT_APP_API_ENDPOINT;

const protocol = process.env.REACT_APP_EOSRPC_PROTOCOL;
const host = process.env.REACT_APP_EOSRPC_HOST;
const port = process.env.REACT_APP_EOSRPC_PORT;

const endpoint = `${protocol}://${host}:${port}`;
const rpc = new JsonRpc(endpoint);
const contract = process.env.REACT_APP_CONTRACT_NAME;

const instance = axios.create({
  baseURL
});

export const getOrganisation = (orgName: string) =>
  instance.get(`/company/${orgName}`).then(res => res.data);

export const getWorkEntries = (orgName: string) =>
  instance.get(`/company/${orgName}/workEntries`).then(res => res.data);

export const getOrganisations = () =>
  instance.get("/companies").then(res => res.data);

export const getRoles = async (orgName: string) => {
  const table = await rpc.get_table_rows({
    json: true,
    code: contract,
    scope: contract,
    table: "roles",
    lower_bound: 0,
    upper_bound: -1,
    limit: 9999,
    index_position: 1
  });

  return table.rows.filter((worker: any) => worker.org === orgName);
};
