import { eos } from "../index";

const tokenContract = process.env.REACT_APP_TOKEN_CONTRACT;

export const createWorkEntry = async (role: string, workForm: any) => {
  return eos.transaction("claimtime", {
    role,
    dechours: workForm.decHours,
    notes: workForm.notes
  });
};

const combineMaxAsset = (
  symbol: string,
  maxSupply: string,
  precision: number
): string => {
  return `${maxSupply}.${"0".repeat(precision)} ${symbol}`;
};

export const createToken = async (
  issuer: string,
  symbol: string,
  maxSupply: string,
  precision: number
) => {
  return eos.transaction(
    "create",
    {
      issuer,
      maximum_supply: combineMaxAsset(symbol, maxSupply, precision)
    },
    tokenContract
  );
};

export const createOrganisation = async (
  owner: string,
  tokensym: string,
  tokencon: string,
  friendlyname: string
) => {
  return eos.transaction("createorg", {
    owner,
    tokensym,
    tokencon,
    friendlyname
  });
};

export const grantActivePermission = async (
  accountGranting: string,
  accountGranted: string
) => {
  const account = await eos.rpc.get_account(accountGranting);

  const activePermissions = account.permissions.find(
    (permission: any) => permission.perm_name === "active"
  ).required_auth;

  if (
    activePermissions.accounts.find(
      (permission: any) => permission.permission.actor === accountGranted
    )
  ) {
    console.log("Permission already granted");
    return;
  }

  return eos.transaction(
    "updateauth",
    {
      account: accountGranting,
      permission: "active",
      auth: {
        ...activePermissions,
        accounts: [
          ...activePermissions.accounts,
          {
            permission: { actor: accountGranted, permission: `active` },
            weight: 1
          }
        ]
      },
      parent: "owner"
    },
    "eosio"
  );
};

export const testReset = async () => eos.transaction("testreset", {});

