import React from "react";
import { TextField, FormControl } from "@material-ui/core";

const CreateOrgForm = ({
  issuer,
  symbol,
  friendlyname,
  tokenCon,
  tokenVariablesKnown,
  handleChange
}: any) => (
  <FormControl>
    {!tokenVariablesKnown && (
      <React.Fragment>
        <TextField
          id="standard-name"
          label="Issuer"
          value={issuer}
          name="issuer"
          onChange={handleChange}
          margin="normal"
        />
        <TextField
          id="standard-name"
          label="Symbol"
          value={symbol}
          name="symbol"
          onChange={handleChange}
          margin="normal"
        />
        <TextField
          id="standard-name"
          label="Token Contract"
          value={tokenCon}
          name="tokenContract"
          onChange={handleChange}
          margin="normal"
        />
      </React.Fragment>
    )}
    <TextField
      id="standard-name"
      label="Friendly Organisation Name"
      value={friendlyname}
      name="friendlyName"
      onChange={handleChange}
      margin="normal"
    />
  </FormControl>
);

export default CreateOrgForm;
