import React, { Component } from "react";
import { Button, Typography, TextField } from "@material-ui/core";

const initialState = {
  worker: "",
  role: "",
  payrate: 0
};

export default class Form extends Component<any, any> {
  state = initialState;

  onChange = (e: any) => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  submit = () => {
    this.props.onSubmit(this.state);
    this.setState(initialState);
  };

  render() {
    console.log(this.props, "on the form");
    return (
      <React.Fragment>
        <Typography variant="h5" gutterBottom>
          Add Team Member
        </Typography>
        <TextField
          id="standard-name"
          label="EOS Account Name"
          placeholder="Account of worker"
          name="worker"
          value={this.state.worker}
          required={true}
          onChange={this.onChange}
          margin="normal"
        />
        <TextField
          id="standard-name"
          label="Role ID"
          name="role"
          placeholder="Dev3"
          value={this.state.role}
          required={true}
          onChange={this.onChange}
          margin="normal"
        />
        <TextField
          id="standard-name"
          label="Pay Rate"
          name="payrate"
          helperText="Tokens rewarded per hour worked"
          type="number"
          placeholder="Dev3"
          value={this.state.payrate}
          required={true}
          onChange={this.onChange}
          margin="normal"
        />

        <Button variant="contained" color="primary" onClick={this.submit}>
          Submit
        </Button>
      </React.Fragment>
    );
  }
}
