import React, { Component } from "react";
import styled from "styled-components";
import SubPage from "./../../SubPage";
import Form from "./Form";
import Content from "./Content";
import Info from "./Info";
import { getRoles } from "../../../api";
import { createTeamMember } from "./../../../transactions";

interface IProps {
  orgname: string;
}

class Team extends Component<IProps> {
  state = {
    teamMembers: []
  };

  componentDidMount() {
    this.fetchWorkers();
  }

  fetchWorkers = async () => {
    const workers = await getRoles(this.props.orgname);
    console.log(workers, "received on team");
    this.setState({ teamMembers: workers });
  };

  onSubmit = async ({ worker, role, payrate }: any) => {
    const result = await createTeamMember(
      this.props.orgname,
      worker,
      role,
      payrate
    );
    console.log(result.transaction_id);
  };

  render() {
    const { orgname } = this.props;
    return (
      <SubPage
        content={<Content teamMembers={this.state.teamMembers} />}
        info={<Info />}
        form={<Form onSubmit={this.onSubmit} />}
      />
    );
  }
}

export default Team;
