import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import HomeIcon from "@material-ui/icons/Home";
import GroupIcon from "@material-ui/icons/Group";
import CodeIcon from "@material-ui/icons/Code";
import { Route, Link } from "react-router-dom";

const NavLink = ({
  to,
  exact,
  strict,
  location,
  activeClassName,
  className,
  activeStyle,
  inactiveClassName,
  style,
  isActive: getIsActive,
  children,
  ...rest
}: any) => (
  <Route
    path={typeof to === "object" ? to.pathname : to}
    exact={exact}
    strict={strict}
    location={location}
  >
    {({ location, match }) => {
      const isActive = !!(getIsActive ? getIsActive(match, location) : match);
      const moddedClassName = `${className || ""} ${
        isActive ? activeClassName : inactiveClassName
      }`;
      return (
        <Link
          to={to}
          className={moddedClassName}
          style={isActive ? { ...style, ...activeStyle } : style}
          {...rest}
        >
          {typeof children === "function" ? children({ isActive }) : children}
        </Link>
      );
    }}
  </Route>
);

const styles = (theme: any) => ({
  root: {
    width: "100%",
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper
  }
});

class SelectedListItem extends React.Component<any> {
  state = {
    selectedIndex: 1
  };

  handleListItemClick = (event: any, index: any) => {
    this.setState({ selectedIndex: index });
  };

  render() {
    const { classes, match } = this.props;

    return (
      <div className={classes.root}>
        <List component="nav">
          <NavLink exact to={match.url}>
            {({ isActive }: any) => (
              <ListItem
                button
                selected={isActive}
                // onClick={event => this.handleListItemClick(event, 0)}
              >
                <ListItemIcon>
                  <HomeIcon />
                </ListItemIcon>
                <ListItemText primary="Home" />
              </ListItem>
            )}
          </NavLink>

          <NavLink exact to={`${match.url}/work-commits`}>
            {({ isActive }: any) => (
              <ListItem
                button
                selected={isActive}
                // onClick={event => this.handleListItemClick(event, 0)}
              >
                <ListItemIcon>
                  <CodeIcon />
                </ListItemIcon>
                <ListItemText primary="Work Entries" />
              </ListItem>
            )}
          </NavLink>

          <NavLink exact to={`${match.url}/team`}>
            {({ isActive }: any) => (
              <ListItem button selected={isActive}>
                <ListItemIcon>
                  <GroupIcon />
                </ListItemIcon>
                <ListItemText primary="Team" />
              </ListItem>
            )}
          </NavLink>
        </List>
      </div>
    );
  }
}

export default withStyles(styles)(SelectedListItem);

