import React, { Component } from "react";
import moment from "moment";
import { getOrganisations } from "../../api";
import { Wrapper, Header } from "./../../styles/Page";
import SubPage from "./../SubPage";
import Info from "./Info";
import Form from "./Form";
import Content from "./Content";
import { Route } from "react-router-dom";
import New from "./New";
import { Typography } from "@material-ui/core";

class BrowserContainer extends Component<any> {
  state = {
    orgs: []
  };

  componentDidMount() {
    this.fetchOrgs();
  }

  fetchOrgs = async () => {
    const orgs = await getOrganisations().then(res =>
      res.map((org: any) => {
        console.log(org);
        const blockTime = moment.utc(org.blockTime);
        const convertedTime = blockTime.local();
        return {
          ...org,
          blockTime: convertedTime.fromNow()
        };
      })
    );
    console.log("received", orgs);
    this.setState({ orgs });
  };

  formSubmit = async (obj: any) => {
    console.log(obj, "received");
  };

  render() {
    const { match } = this.props;
    console.log(this.props, "was props");
    console.log(`${match.url}/new`);
    return (
      <Wrapper>
        <Header>
          <Typography variant="h3">Organisation Browser</Typography>
        </Header>
        <SubPage
          content={<Content organisations={this.state.orgs} />}
          info={<Info />}
          form={<Form onSubmit={this.formSubmit} />}
        />

        <Route path={`/new`} render={props => <New />} />
      </Wrapper>
    );
  }
}

export default BrowserContainer;
