import React from "react";
import WorkTime from "./WorkTime";
import {
  Table,
  TableBody,
  TableCell,
  TableRow,
  TableHead
} from "@material-ui/core";

const Content = ({ workEntries }: any) => (
  <React.Fragment>
    <Table>
      <TableHead>
        <TableRow>
          <TableCell>Worker</TableCell>
          <TableCell>Hours</TableCell>
          <TableCell>Awarded</TableCell>
          <TableCell>Notes</TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
        {workEntries.map((entry: any, index: number) => (
          <WorkTime key={index} {...entry} />
        ))}
      </TableBody>
    </Table>
  </React.Fragment>
);

export default Content;
