import React from "react";
import { withStyles } from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import CreateTokenForm from "./CreateTokenForm";
import CreateOrgForm from "./CreateOrgForm";
import MintForm from "./MintForm";
import {
  createToken,
  createOrganisation,
  grantActivePermission
} from "../../../transactions";

const defaultTokenContract = process.env.REACT_APP_TOKEN_CONTRACT;
const arbaroContract: string = process.env.REACT_APP_CONTRACT_NAME!;

const styles = (theme: any) => ({
  root: {
    width: "90%",
    marginLeft: "auto",
    marginRight: "auto"
  },
  button: {
    marginRight: theme.spacing.unit
  },
  instructions: {
    marginTop: theme.spacing.unit,
    marginBottom: theme.spacing.unit
  }
});

function getSteps() {
  return ["Create Token", "Create Organisation", "Grant Permission"];
}

class HorizontalLinearStepper extends React.Component<any, any> {
  state = {
    activeStep: 0,
    skipped: new Set(),
    issuer: "",
    symbol: "",
    maxSupply: "",
    precision: 4,
    tokenCon: defaultTokenContract || "",
    friendlyName: "",
    tokenVariablesKnown: false
  };

  getStepContent = (step: number) => {
    switch (step) {
      case 0:
        return (
          <CreateTokenForm
            issuer={this.state.issuer}
            symbol={this.state.symbol}
            maxSupply={this.state.maxSupply}
            precision={this.state.precision}
            handleChange={this.handleChange}
          />
        );
      case 1:
        return (
          <CreateOrgForm
            tokenCon={this.state.tokenCon}
            friendlyName={this.state.friendlyName}
            issuer={this.state.issuer}
            symbol={this.state.symbol}
            maxSupply={this.state.maxSupply}
            tokenVariablesKnown={this.state.tokenVariablesKnown}
            handleChange={this.handleChange}
          />
        );
      case 2:
        return <MintForm />;
      default:
        return "Unknown step";
    }
  };

  handleChange = (e: any) => {
    this.setState({ [e.target.name]: e.target.value });
  };

  isStepOptional = (step: number) => step === 0;

  handleNext = async () => {
    const {
      activeStep,
      issuer,
      maxSupply,
      symbol,
      tokenCon,
      friendlyName,
      precision
    } = this.state;
    if (activeStep == 0) {
      const result = await createToken(issuer, symbol, maxSupply, precision);
      this.setState({ tokenVariablesKnown: true });
    } else if (activeStep == 1) {
      const result = await createOrganisation(
        issuer,
        `${precision},${symbol}`,
        tokenCon,
        friendlyName
      );
      console.log("made organisation", result.transaction_id);
    } else if (activeStep == 2) {
      const result = await grantActivePermission(issuer, arbaroContract);
      console.log("ressa", result);
    }
    let { skipped } = this.state;
    if (this.isStepSkipped(activeStep)) {
      skipped = new Set(skipped.values());
      skipped.delete(activeStep);
    }
    this.setState({
      activeStep: activeStep + 1,
      skipped
    });
  };

  createToken = async () => {};

  handleBack = () => {
    this.setState((state: any) => ({
      activeStep: state.activeStep - 1
    }));
  };

  handleSkip = () => {
    const { activeStep } = this.state;
    if (!this.isStepOptional(activeStep)) {
      // You probably want to guard against something like this,
      // it should never occur unless someone's actively trying to break something.
      throw new Error("You can't skip a step that isn't optional.");
    }

    this.setState((state: any) => {
      const skipped = new Set(state.skipped.values());
      skipped.add(activeStep);
      return {
        activeStep: state.activeStep + 1,
        skipped
      };
    });
  };

  handleReset = () => {
    this.setState({
      activeStep: 0
    });
  };

  isStepSkipped(step: number) {
    return this.state.skipped.has(step);
  }

  render() {
    const { classes } = this.props;
    const steps = getSteps();
    const { activeStep } = this.state;

    return (
      <div className={classes.root}>
        <Stepper activeStep={activeStep}>
          {steps.map((label, index) => {
            const props = {};
            const labelProps = {};
            if (this.isStepOptional(index)) {
              // @ts-ignore
              labelProps.optional = (
                <Typography variant="caption">Optional</Typography>
              );
            }
            if (this.isStepSkipped(index)) {
              // @ts-ignore
              props.completed = false;
            }
            return (
              <Step key={label} {...props}>
                <StepLabel {...labelProps}>{label}</StepLabel>
              </Step>
            );
          })}
        </Stepper>
        <div>
          {activeStep === steps.length ? (
            <div>
              <Typography className={classes.instructions}>
                All steps completed - you&apos;re finished
              </Typography>
              <Button onClick={this.handleReset} className={classes.button}>
                Reset
              </Button>
            </div>
          ) : (
            <div>
              <Typography className={classes.instructions}>
                {this.getStepContent(activeStep)}
              </Typography>
              <div>
                <Button
                  disabled={activeStep === 0}
                  onClick={this.handleBack}
                  className={classes.button}
                >
                  Back
                </Button>
                {this.isStepOptional(activeStep) && (
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={this.handleSkip}
                    className={classes.button}
                  >
                    Skip
                  </Button>
                )}
                <Button
                  variant="contained"
                  color="primary"
                  onClick={this.handleNext}
                  className={classes.button}
                >
                  {activeStep === steps.length - 1 ? "Finish" : "Next"}
                </Button>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(HorizontalLinearStepper);
