import React, { Component } from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

import { eos } from "../index";
import { AppBar, Toolbar, Typography, Button } from "@material-ui/core";
import IconButton from "@material-ui/core/IconButton";
import HomeIcon from "@material-ui/icons/Home";
import { createStyles, withStyles, WithStyles } from "@material-ui/core/styles";

const styles = createStyles({
  root: {
    flexGrow: 1
  },
  grow: {
    flexGrow: 1
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
    color: "white"
  }
});

const Wrapper = styled.div`
  display: grid;
  grid-template-columns: 2fr 2fr 8fr 2fr 2fr;
  grid-template-areas: ". b s p .";
`;

const Back = styled.div`
  justify-self: center;
  align-self: center;
  grid-area: b;
`;

const TitleDiv = styled.div`
  justify-self: center;
  align-self: center;
  grid-area: s;
`;

const Scatter = styled.div`
  align-self: center;
  justify-self; center;
  grid-area: p;
  display: grid;
  grid-template-columns: 1fr 2fr;
`;

const Center = styled.div`
  justify-self: center;
  align-self: center;
`;

const AvatarPicture = styled.img`
  border-radius: 50%;
  width: 45px;
`;

class AppHeader extends Component {
  state = {
    authed: false,
    accountName: ""
  };

  componentDidMount() {
    setTimeout(() => {
      this.login();
    }, 900);
  }

  login = async () => {
    try {
      const result: any = await eos.login();
      this.setState({
        accountName: result.name,
        authed: true
      });
    } catch (e) {
      console.log(e);
    }
  };

  logout = async () => {
    this.setState({
      accountName: "",
      authed: false
    });
  };

  render() {
    const { authed, accountName } = this.state;
    return (
      <AppBar position="static">
        <Toolbar>
          <Link to="/">
            <IconButton style={styles.menuButton} aria-label="Menu">
              <HomeIcon />
            </IconButton>
          </Link>
          <Typography variant="h6" color="inherit" style={styles.grow}>
            Arbaro
          </Typography>
          <Button onClick={authed ? this.logout : this.login} color="inherit">
            {authed ? `Log out ${accountName}` : "Sign in Scatter"}
          </Button>
        </Toolbar>
      </AppBar>
    );
  }
}

export default AppHeader;
