import React, { Component } from "react";
import styled from "styled-components";
import Work from "../pages/Organisation/Work";
import Team from "../pages/Organisation/Team";
import Home from "../pages/Organisation/Home";
import "./NavCss.css";
import {
  BrowserRouter as Router,
  Route,
  Link,
  NavLink
} from "react-router-dom";
import axios from "axios";
import CompanyNav from "./CompanyNav";
import { AppBar, Toolbar, Typography } from "@material-ui/core";
import { getOrganisation } from "./../api";

const Wrapper = styled.div`
  display: grid;
  grid-template-columns: 2fr 2fr 8fr 2fr 2fr;
  grid-template-areas:
    ". . h . ."
    ". m c i ."
    ". m c f .";
`;

const Header = styled.div`
  justify-self: center;
  align-self: center;
  grid-area: h;
  margin-top: 10px;
`;

const Menu = styled.div`
  grid-area: m;
  line-height: 1.5;
`;

class Company extends Component<any> {
  state = {
    friendlyName: this.props.match.params.orgname
  };

  componentDidMount() {
    this.fetchOrg();
  }

  fetchOrg = async () => {
    try {
      const { friendlyname } = await getOrganisation(
        this.props.match.params.orgname
      );
      this.setState({ friendlyName: friendlyname });
    } catch (e) {
      console.log(e);
    }
  };

  render() {
    const { orgname } = this.props.match.params;
    const { match } = this.props;
    return (
      <Wrapper>
        <Header>
          <Typography variant="h3">{this.state.friendlyName}</Typography>
        </Header>
        <Menu>
          <CompanyNav match={match} />
        </Menu>

        <Route exact path={`${match.url}`} component={Home} />
        <Route
          exact
          path={`${match.url}/work-commits`}
          render={props => <Work orgname={orgname} />}
        />
        <Route
          exact
          path={`${match.url}/team`}
          render={props => <Team orgname={orgname} />}
        />
      </Wrapper>
    );
  }
}

export default Company;
