import React from "react";
import accounting from "accounting-js";
// import { TableRow, TableCell, Text } from "@aragon/ui";
import {
  Table,
  TableBody,
  TableCell,
  TableRow,
  TableHead
} from "@material-ui/core";

const WorkTime = ({
  worker,
  decHours,
  rewardAmount,
  rewardSymbol,
  notes
}: any) => (
  <TableRow>
    <TableCell>{worker}</TableCell>
    <TableCell>{decHours}</TableCell>
    <TableCell>
      {`${accounting.formatNumber(rewardAmount, {
        precision: 0
      })} ${rewardSymbol}`}
    </TableCell>
    <TableCell>{notes}</TableCell>
  </TableRow>
);

export default WorkTime;
